@font-face {
    font-family: 'Proxima';
    src:  url('./fonts/ProximaNova-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'Proxima';
    src:  url('./fonts/ProximaNova-Extrabld.woff') format('woff');
    font-weight: 800;
    font-style: normal;
}
@font-face {
    font-family: 'Avenir';
    src:  url('./fonts/AvenirLTStd-Medium.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'Avenir';
    src:  url('./fonts/AvenirLTStd-Black.woff') format('woff');
    font-weight: 800;
    font-style: normal;
}

body {
  margin: 0;
  font-family: 'Avenir', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding-top:1em;
  background-color: #825eeb;
  border-left: 10px solid #825eeb;
  border-right: 10px solid #825eeb;
  
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root,.App
{
  height:100%;
  background-color:#825eeb
}

.CardHeading img
{
  width:1em;
  float:left;
  margin-left:.7em;
  margin-right:0.5em;
  opacity: .5;
}

.Logo
{
  height:2em;
  position: relative;
  bottom: -0.5em;
}

.TitleHead 
{
  position: relative;
  top: -.8em;
  font-family: 'Proxima'!important;
  letter-spacing: -2px!important;
}


.TitleHead  span
{
  font-size:9px;
  letter-spacing: 0;
  color:#CCC;
  font-family: 'Avenir';
  position: absolute;
  top:63px;
  left:75px;
}


.SecondRow
{
  margin-top:-1.5em;
  border-top:1px solid #DDD;
  background-color: #EEE;
}

.ProductTabs
{
  margin-left:1em;
}

#Tab_Chat, #Tab_Desk
{
  cursor: not-allowed;
  color:#AAA;
  background-color: #DDD;
}



.Cards
{
  position:relative;
  column-gap: 0;
  top:1em;
}

.Column.Single .Cards.ColCount1{ column-count: 1; }
.Column.Single .Cards.ColCount2{ column-count: 2; }
.Column.Single .Cards.ColCount3{ column-count: 3; }
.Column.Single .Cards.ColCount4{ column-count: 4; }
.Column.Single .Cards.ColCount5{ column-count: 5; }
.Column.Single .Cards.ColCount6{ column-count: 6; }
.Column.Single .Cards.ColCount7{ column-count: 7; }
.Column.Single .Cards.ColCount8{ column-count: 8; }
.Column.Single .Cards.ColCount9{ column-count: 9; }

.Column.Multi .Card
{
  width:100%;
}
.Column.Single .Card
{
  display: inline-block;
}
.Column.Multi .Card span.CardHeading
{
  word-break: break-all;
  word-wrap: break-word;
  width: auto;
}
.Column.Multi .Card 
{
  margin-left: 1em;
}

.Card{
  
  break-inside: avoid-column;
  margin-bottom:1.5em;
  margin-left:25px;
  padding-bottom:1em;
}

.Card{
}

.Card span.CardHeading
{
  color:#825eeb;
  font-weight: 600;
  font-size:1.2em;
  display: block;
  margin-bottom: 1em;
  margin-top: .5em;
  position: relative;
  top:.5em;
  margin-left:2.2em;
  font-family: 'Proxima';
  margin-right: 1em;
  min-width: 75%;
  max-width: 250px;
}

.Card .cardPreview
{
  text-align: center;
  padding-bottom:.5em;
}

.Card span.CardHeading img
{
  position:absolute;
  left:-2.2em;
}
.Card:hover{
  position: relative;
  left:0.1em;
}
.Card:hover img{
  opacity: 1;
}

h4{
  margin:0;
  font-size: 1.3em;
  overflow: auto;
}

h4 .left{float:left; margin-left:1em; line-height: 1.5em;}
h4 .right{float:right}

h4 .right strong{font-weight:800; font-size:.8em; line-height:1.5em; height:1.5em; margin-right: .5em; position: relative; top:-.1em;}

.ProductTabs button{
  color:#CCC!important;
  background-color:#EEE!important;
  line-height: 1em;
}


.ProductTabs button#Tab_Calls{
  color:#FFF!important;
  font-weight: 800;
  background-color:#825eeb!important;
  border:2px solid #825eeb!important;
  cursor: pointer;
}

pre
{

  white-space: pre-wrap;
}


.Tree .branch img{
  height:1em;
  width:1em;
  margin-right: .5em;
  opacity: 0.5;
}

.Column .Heading
{
  text-align: center;
  color:#dcd2f9;
  font-weight: 800;
  font-size: 1.2em;
  padding-top:.5em;
  padding-bottom:.5em;
  margin-bottom:0;
  background-color: #270e71;
  border-right:1px solid  #270e71;;
  border-top:1px solid #a68ef1;
  vertical-align: middle;
  height: 3.6em;
  overflow: hidden;
  line-height: 1em;
}



.Column
{
  float:left;
  width:50%;
  border-left:1px solid #a68ef1;
  border:1px solid #a68ef1;
  padding-bottom: 3em;
  margin-bottom: 3em;
  margin-left:-1px;
  margin-top:-1px;
}

.ThirdRow + .Column
{
  margin-left:0px;
}

.Search button
{
  display: none;
}



.ThirdRow h4
{
  margin-top:-10px;
  padding-right: .1em;
  position: relative;
  bottom:-.2em;
  font-size: 1.5em;
}

.NavTable
{
  margin-top:-1.5em;
  font-size: 10px;
  color:#AAA;
  width: 100%;
}
.NavTable .labels td
{
  padding-left: .5em;
}
.NavTable .labels td.last
{
  text-align: right;
  padding-right: 9em;
  width: 80%;
}



.NavTable button{
  white-space: nowrap;
}

.cookBookIcon
{
  float:left;
  margin-right:2em;
  width:50px;
  opacity: 0.5;
}
.Recipe 
{
  position: relative;
}
.Recipe:hover
{
  bottom: 3px;
  background-color: #FFF;
}

.Recipe div
{
  margin-left: 5.1em;
  color:#999;
}
.Recipe h2
{

  margin-left: 4em;
}
.Recipe img
{
  position: absolute;
}

.CardTable
{
  float: right;
  margin-top:-3px;
}

.CardTable .toprow td{

  font-size:10px;
  padding:0;
}

.Card > strong{
  border-top-left-radius:1em;
  border-top-right-radius:1em;
  line-height:2em;
  height:2em;
  overflow: hidden;
  width:100%;
  margin-bottom: -1em;
  
}

.WindowTitle img
{
  height:3em;
  float:left;
  margin-right:1em;
  opacity: 0.5;
  position: absolute;
}
.WindowTitle h2
{
  line-height:1.5em;
  font-size: 2em;
  font-weight: 800;
  font-family: 'Proxima'!important;
  color:#825eeb;
  margin-left:2em;
  padding-top: 10px;
  line-height: 1em;
}

.cardHeadingNoHighlight
{
  color:#a68ef1;
}
.cardHeadingHighlight
{
  color:#212242;
}


.searchResult img
{
  height:1.5em;
  width:1.5em;
  margin-right: .5em;
  margin-left: .5em;
  position: relative;
  top:0.35em;
  opacity: 0.3;
}
.searchResult 
{
  line-height:2.5em;
  font-size:1.5em;
  color:#333;
  overflow:hidden;
  height:2.5em;
}
.searchResult strong
{
  position: relative;
  top:-0.35em;
}

.searchHighlight
{
  background-color: rgb(255, 255, 240);
  border-bottom:3px double #EEE;
  color:rgb(49, 27, 27);
}

.searchResult.active
{
  outline: 3px solid #825eeb;
  background-color: #eeeaf7;
}

.searchResult.active .searchHighlight
{
  background-color: #eeeaf7;
  border-color:#825eeb
}

.titleLabel
{
  position: absolute;
  left:6em;
  top:.55em;
  font-size: 12px;
  color:#CCC;
  font-family: Avenir!important;
}
.infoBox
{
  padding:2em;
  padding-top:0;
}
.infoBox h2
{

  color:#825eeb
}
.infoBox h2 img
{

  height:1em;
  width:1em;
  opacity: 0.5;
  position: relative;
  top:0.1em;
}
.infoBox span
{

  font-family: Avenir!important;
  font-size: 1em;
}

.Card button{
  margin-top:.5em;
}

.tree
{
  text-align: left;
  margin:1em;
  margin-bottom: -1em;
  font-size:12px;
  padding-left: 1.5em;
  border-top: 1px solid #CCC;
  padding-top:1em;
}
.branch
{
  position: relative;
  margin-left: 1em;
  line-height: 1em;
  margin-bottom: .5em;
  color: #888;
}
.branch img
{
  width:1em;
  position: absolute;
  left:-1.5em;
  opacity: 0.5;
}

.status span
{
  color:#AAA;
  font-size: 12px;
  letter-spacing: 1px;
}


.cardPreview strong
{
  line-height: 18px;
}